.resum-container{
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content:center;
    align-items: center;
    flex-direction: column;
    .top{
        h2{
            text-align: center;
            margin-bottom: 10px;
            color: greenyellow;
            &:hover{
                color:burlywood;
            }
        }
        margin-bottom: 2rem;
    }
    .bottom{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 3rem;
        .blog1,.blog2{
            background-color:aliceblue;
            margin: 0.5rem;
            padding: 1rem;
            border-radius: 0.5rem;
            color: black;
            margin-bottom: 1rem;
        }
    }
}
@media screen and (max-width:900px) {
    .resum-container{
        .bottom{
            display: flex;
            flex-wrap: wrap;
            gap: 1rem;
            flex-direction: column;
            cursor: pointer;
            .blog1,.blog2{
                margin-bottom: 1rem;
            }
        }
    }
    
}