.footer-container{
    display: flex;
    flex-direction: column;
    background-color:aliceblue;
    width: 100%;
    padding: 10px;
    color:black;
    .top{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        margin-top: 10px;
           .about{
            display: flex;
            justify-content: space-between;
            gap: 20px;
            align-items: center;
            margin-top: 20px;
           }
           .icons{
            display: flex;
            gap: 30px;
            align-items: center;
            justify-content: space-between;
            margin-top: 20px;
           }
        }
    .bottom{
        display: flex;
        justify-content: space-between;
        margin-top: 1rem;
        background-color:rgba(0, 0, 0, 0.365);
        padding: 10px 30px;
        border-top: 1px solid gainsboro;
        .left{
            display: flex;
             align-items: flex-start;
        }
        .right{
            display: flex;
            align-items: center;
            gap: 1rem;
            justify-content: space-between;
        }
    }
}
@media screen and (max-width:900px) {
    .footer-container{
      .top{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .about{
            display: flex;
        }
      }  
    }
    .bottom{
        display: flex;
        gap: 1rem;
        align-items: center;
        flex-direction: column-reverse;
        .right{
            display: flex;
            flex-direction: column;
            gap: 0;
        }
    }
}